/** Media Queries **/
.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__heading {
  font-size: 3rem;
  font-weight: bold;
}
.header__nav {
  display: none;
}
.header__nav a {
  padding: 7rem 0;
  border-bottom: 0px solid #f5f5f5;
}
.header__nav a:hover {
  color: #1d2025;
  border-bottom: 0.4rem solid #ff7d1a;
  padding-bottom: 0px;
}
.header__link {
  display: flex;
  padding: 2rem 0;
}
.header__right {
  width: 7rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__right img:nth-child(1):hover {
  filter: brightness(10) invert(1);
}
.header__right img:nth-child(2) {
  width: 2.5rem;
  padding-bottom: 0.3rem;
}
.header img {
  cursor: pointer;
}

#header__menu:hover {
  transition: filter 1s;
  filter: brightness(10) invert(1);
}

@media (min-width: 768px) {
  #header__menu {
    display: none;
  }

  .header {
    gap: 5rem;
    margin-top: 2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 2px solid #f5f5f5;
  }
  .header__heading {
    font-weight: bold;
    width: 28%;
  }
  .header__nav {
    width: 70rem;
    display: flex;
    justify-content: flex-start;
    gap: 3rem;
  }
  .header__nav a {
    color: #68707d;
  }
  .header__right {
    width: 15rem;
    display: flex;
    align-items: center;
    justify-content: space-around;
  }
  .header__right img:nth-child(2) {
    width: 4rem;
  }
}
.main__cont {
  display: grid;
}
.main__textheading {
  color: #ff7d1a;
  text-transform: uppercase;
  font-size: 1rem;
  line-height: 1.8;
  font-weight: bold;
  letter-spacing: 0.2rem;
}
.main__heading {
  font-weight: bold;
}
.main__products {
  width: 100%;
}
.main__thumbs {
  display: none;
}
.main__description {
  color: #68707d;
}
.main__prizecontainer {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  align-items: center;
}
.main__prize {
  font-weight: bold;
  font-size: 3rem;
  margin-right: 1rem;
}
.main__discount {
  color: #ff7d1a;
  width: 5rem;
  text-align: center;
  background-color: #ffede0;
  border-radius: 0.5rem;
}
.main__prize--discounted {
  justify-self: flex-end;
  color: #b6bcc8;
  font-weight: bold;
  text-decoration: line-through;
}
.main__form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.main__input {
  display: flex;
  background-color: #f5f5f5;
  height: 5rem;
  width: 90%;
  justify-content: space-between;
  align-items: center;
  border-radius: 1rem;
  overflow-y: hidden;
}
.main__input--icon {
  padding: 0 2rem;
  cursor: pointer;
}
.main__input--icon:hover {
  transition: opacity 500ms;
  opacity: 0.2;
}
.main__btn {
  margin: 2rem 0 6rem;
  width: 90%;
  height: 5rem;
  background-color: #ff7d1a;
  color: #FFFFFF;
  font-weight: bold;
  font-family: "Kumbh Sans", sans-serif;
  border: none;
  border-radius: 1rem;
  cursor: pointer;
}
.main__btn:hover {
  transition: opacity 500ms;
  opacity: 0.5;
  box-shadow: 0 3px 7px #ffede0;
}
.main__btn img {
  margin-right: 1rem;
  width: 1.5rem;
  height: 1.5rem;
}

@media (min-width: 768px) {
  .main {
    margin-top: 10rem;
  }
  .main__cont {
    grid-template-columns: repeat(2, 1fr);
    gap: 10rem;
  }
  .main__products {
    display: grid;
    grid-template-rows: 75% 25%;
  }
  .main__productimg {
    border-radius: 1rem;
    height: 100%;
    margin: 0 auto;
    display: block;
    cursor: pointer;
  }
  .main__thumbs {
    grid-row: 2/3;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    align-items: center;
  }
  .main__thumbs img {
    width: 75%;
    border-radius: 1rem;
    justify-self: center;
    cursor: pointer;
  }
  .main__thumbs img:nth-child(1) {
    opacity: 0.2;
  }
  .main__blocktext {
    margin-top: 5rem;
  }
  .main__textheading {
    font-size: 1.4rem;
  }
  .main__heading span {
    display: block;
  }
  .main__prizecontainer {
    width: 25rem;
    display: grid;
    grid-template-rows: 60% 40%;
    grid-template-columns: 50% 50%;
    align-items: center;
  }
  .main__prize {
    margin: 0;
  }
  .main__prize--discounted {
    justify-self: flex-start;
    margin: 0;
  }
  .main__discount {
    grid-column: 2/3;
    grid-row: 1/2;
  }
  .main__form {
    margin-top: 3rem;
    flex-direction: row;
    justify-content: center;
    gap: 1.5rem;
    width: 70%;
  }
  .main__input {
    width: 40%;
  }
  .main__btn {
    margin: 0;
    width: 60%;
  }
}
.footer {
  text-align: center;
}
.footer__heading {
  color: #68707d;
}
.footer__heading span {
  color: #1d2025;
}

.main__modal {
  background-color: #FFFFFF;
  overflow: hidden;
  position: absolute;
  left: 1rem;
  top: 9rem;
  height: 30%;
  width: 90%;
  border-radius: 1rem;
  display: grid;
  grid-template-rows: 20% 80%;
  align-items: center;
  box-shadow: 0 3px 7px rgba(0, 0, 0, 0.3);
  transition: all 1s;
  animation: growDown 500ms ease-in-out forwards;
  transform-origin: top center;
  z-index: 10;
}
.main__modal h3 {
  font-size: 1.6rem;
  font-weight: bold;
  padding: 3rem 2rem;
  border-bottom: 1px solid #b6bcc8;
}
.main__modal--item {
  text-align: center;
  height: 20rem;
}
.main__modal--top {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  height: 15rem;
}
.main__modal--top img:nth-child(1) {
  width: 10rem;
  height: 10rem;
  border-radius: 2rem;
  padding: 1rem 1rem;
}
.main__modal--trash {
  padding: 0 1rem;
  justify-self: flex-end;
  margin: 0 auto;
  cursor: pointer;
}
.main__modal--trash:hover {
  transition: filter 1s;
  filter: brightness(10) invert(1);
}
.main__modal--desc {
  display: flex;
  flex-direction: column;
}
.main__modal--desc h4 {
  font-size: 1.4rem;
  margin: 0;
  padding-top: 2rem;
  font-weight: 400;
  color: #68707d;
}
.main__modal--prizes {
  display: flex;
}
.main__modal--prizes p:nth-child(1) {
  font-size: 1.4rem;
  margin: 0;
  color: #68707d;
}
.main__modal--prizes p:nth-child(1) span:nth-child(3) {
  font-weight: bold;
  color: #000;
}
.main__modal--prizes p:nth-child(1) span:last-of-type {
  font-weight: bold;
  color: #000;
}
.main__modal--btn {
  border: none;
  font-weight: bold;
  color: #FFFFFF;
  background-color: #ff7d1a;
  width: 90%;
  height: 5rem;
  border-radius: 1rem;
  cursor: pointer;
}
.main__modal--btn:hover {
  transition: opacity 500ms;
  opacity: 0.5;
  box-shadow: 0 3px 7px #ffede0;
}
.main__modal--empty {
  justify-self: center;
  color: #b6bcc8;
}
@media (min-width: 768px) {
  .main__modal {
    width: 30%;
    left: 52rem;
    top: 15rem;
  }
}
@media (min-width: 992px) {
  .main__modal {
    left: 75rem;
    width: 25%;
  }
}
@media (min-width: 1440px) {
  .main__modal {
    left: 110rem;
    width: 20%;
  }
}

@keyframes growDown {
  0% {
    transform: scaleY(0);
  }
  80% {
    transform: scaleY(1.1);
  }
  100% {
    transform: scaleY(1);
  }
}
body {
  overflow: scroll;
}

@media (min-width: 375px) {
  .modal {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 0;
    background-color: #1d2025;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.5);
  }
  .modal .swiper {
    z-index: 5;
  }
  .modal__close {
    cursor: pointer;
    position: absolute;
    top: 14rem;
    right: 19%;
    width: 3rem;
    filter: brightness(0) invert(1);
  }
}
@media (min-width: 375px) and (min-width: 768px) {
  .modal__close {
    right: 23%;
  }
}
@media (min-width: 375px) and (min-width: 992px) {
  .modal__close {
    right: 28%;
  }
}
@media (min-width: 375px) and (min-width: 1440px) {
  .modal__close {
    right: 33%;
  }
}
@media (min-width: 375px) and (min-width: 1480px) {
  .modal__close {
    right: 35%;
  }
}
.mobile {
  position: absolute;
  height: 100%;
  width: 100%;
  z-index: 10;
  background-color: rgba(0, 0, 0, 0.5);
  transition: all 1s;
  animation: growDown 500ms ease-in-out forwards;
  transform-origin: top center;
}
.mobile__cont {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  background-color: #FFFFFF;
  width: 60%;
  height: 100%;
}
.mobile__cont img {
  width: 6rem;
  cursor: pointer;
  padding: 2rem;
}
.mobile__cont img:hover {
  transition: filter 1s;
  filter: brightness(10) invert(1);
}
.mobile__nav {
  display: flex;
  flex-direction: column;
  padding: 0 2rem;
  gap: 2rem;
  font-weight: bold;
  margin-top: 8rem;
}
.mobile__nav a {
  color: #b6bcc8;
  width: 50%;
}
.mobile__nav a:hover {
  transition: color 500ms;
  color: #1d2025;
  border-bottom: 0.2rem solid #ff7d1a;
  padding-bottom: 0px;
}

.notification {
  position: absolute;
  width: 100%;
  height: 10%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 40;
  text-align: center;
  transition: all 1s;
  animation: growDown 500ms ease-in-out forwards;
  transform-origin: top center;
}
.notification__heading {
  font-size: 1.6rem;
  color: #ff7d1a;
}

.items {
  position: relative;
  bottom: 1rem;
  right: 1rem;
  width: 2rem;
  height: 1.5rem;
  background-color: #ff7d1a;
  border-radius: 40%;
  z-index: 2;
  color: #FFFFFF;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media (max-width: 768px) {
  .main__products {
    display: none;
  }
}
.swiper {
  width: 35rem;
  opacity: 1;
}
.swiper-slide {
  display: flex;
  justify-content: center;
  align-items: center;
}

@media (min-width: 768px) {
  .phone-swiper {
    display: none;
  }

  .modal .swiper {
    width: 50rem;
    height: 60rem;
  }

  .swiper2 {
    width: 50rem;
    height: 20rem;
    justify-content: center;
  }
}
