body {
  font-family: "Kumbh Sans", sans-serif;
  font-size: 1.6rem;
  font-weight: 300;
  height: 100vh;
  overflow-x: hidden;
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.containerXl {
  width: 95%;
  max-width: 1400px;
  margin: 0 auto;
}

img {
  max-width: 100%;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

button {
  text-decoration: none;
}

.hidden {
  display: none;
}

.containerAbs {
  width: 100%;
  height: 100%;
  position: absolute;
  background: #eee;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
  overflow-y: hidden;
}

span {
  overflow-y: hidden;
}

h1, h2 {
  font-weight: 400;
}

h1 {
  font-size: 4.4rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 2.4rem;
  font-weight: 300;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
  color: #000;
}

p {
  font-size: 1.6rem;
  line-height: 1.8;
}